import * as React from 'react'
import './calculator.scss'
import Keypad from './keypad'
import Display from '../components/display'

export interface CalculatorProps {}

const Calculator: React.FC = (props: CalculatorProps) => {
    const [currentInput, setCurrentInput] = React.useState<Array<string> | Array<never>>([])

    const onButtonClicked = (value: string) => {
        // If button pressed is not a number, use following logic
        if (isNaN(value as any)) {
            switch (value) {
                case 'C':
                    setCurrentInput([])
                    return
                case '.':
                    if (!currentInput.length) {
                        joinNumber(['0', '.'])
                        return
                    }
                    joinNumber(value)
                    return
                default:
                    return
            }
        }

        // If 0 is pressed before any other number, don't allow aditional 0's
        if (!currentInput.length && value === '0') {
            return
        }

        joinNumber(value)
    }

    const joinNumber = (value: string | Array<string>) => {
        const newCurrentInput = [...currentInput]

        // Don't allow more than 1 decimal sign
        if (value === '.' && newCurrentInput.some(element => element.includes('.'))) {
            return
        }

        console.log([newCurrentInput.concat(value).join('')])
        setCurrentInput([newCurrentInput.concat(value).join('')])
    }

    return (
        <main className="calculator__container">
            <Display input={currentInput.length === 0 ? undefined : currentInput.join('')} />
            <Keypad onButtonClicked={onButtonClicked} />
        </main>
    )
}

export default Calculator
